.statistics {
    display: flex;
    padding-top: 20px;
    margin: auto;
  }
  .statList {
    display: flex;
    flex-direction: column;
    width: 300px;
    justify-content: center;
    margin: auto;
  }
  .statItem {
    margin-bottom: 15px;
    justify-content: center;
    display: flex;
    justify-content: space-between;
    align-items: center;
  }
  .text {
    line-height: 5px;
    color: mediumblue;
    font-size: 16px;
    font-weight: 700;
  }
  .description {
    line-height: 5px;
    color: mediumblue;
    font-size: 16px;
    font-weight: 700;
    margin-left: 10px;
  }